import {
  AppBar,
  Container,
  Toolbar,
  Typography,
  Select,
  MenuItem,
  createTheme,
  ThemeProvider,
  FormControl,
} from "@mui/material";
import React from "react";
import { useNavigate } from "react-router-dom";
import { useGlobalContext } from "../Context/CryptoContext";

const Header = () => {
  const darkTheme = createTheme({
    palette: {
      mode: "dark",
    },
  });

  const navigate = useNavigate();

  const { currency, setCurrency, currencies } = useGlobalContext();

  return (
    <ThemeProvider theme={darkTheme}>
      <AppBar color="secondary" position="fixed">
        <Container>
          <Toolbar
            style={{
              display: "flex",
              alignItems: "center`",
              justifyContent: "space-between",
            }}
          >
            <Typography
              color={"#FFD93D"}
              fontWeight={"bold"}
              fontSize={"32px"}
              onClick={() => navigate("/")}
            >
              Crypto Currency
            </Typography>

            <FormControl sx={{ m: 1, minWidth: 120 }}>
              <Select
                variant="outlined"
                value={currency}
                onChange={(e) => setCurrency(e.target.value)}
              >
                {
                  currencies?.map((e) => {
                    return <MenuItem value={e}>{e.toUpperCase()}</MenuItem>
                  })
                }
              </Select>
            </FormControl>
          </Toolbar>
        </Container>
      </AppBar>
    </ThemeProvider>
  );
};

export default Header;
