import axios from "axios";
import React from "react";
import { TrendingCoins } from "../Config/Api";
import { useGlobalContext } from "../Context/CryptoContext";
import { useState } from "react";
import { useEffect } from "react";
import AliceCarousel from "react-alice-carousel";
import { Link } from "react-router-dom";

const Carousel = () => {
  const { currency, currencySymbol } = useGlobalContext();
  const [trending, setTrending] = useState([]);

  const fetchTrendingCoins = async (url) => {
    /* const res = await axios.get(url);
    const data = res.data; */

    const res = await fetch(url);
    const data = await res.json();
    
    console.log(data);
    setTrending(data);
  };

  useEffect(() => {
    fetchTrendingCoins(TrendingCoins(currency));
  }, [currency]);

  const responsive = {
    0: {
      items: 2,
    },
    512: {
      items: 4,
    },
  };

  const items = trending.map((elem, index) => {
    const {
      image,
      name,
      id,
      price_change_percentage_24h,
      symbol,
      current_price,
    } = elem;
    let profit = price_change_percentage_24h >= 0;

    return (
      <Link
        className="carousel-item"
        key={index}
        to={`/coin/${id}`}
        style={{ textDecoration: "none" }}
      >
        <div className="image">
          <img src={image} alt={name} />
        </div>
        <div className="desc">
          <div>
            <span className="profit">{symbol}</span>

            <span className={`${profit ? "green" : "red"}`}>
              {profit
                ? `+ ${price_change_percentage_24h.toFixed(2)}%`
                : `${price_change_percentage_24h.toFixed(2)}%`}
            </span>
          </div>
          <span className="price">{`${currencySymbol} ${current_price.toLocaleString(
            "en-IN"
          )}`}</span>
        </div>
      </Link>
    );
  });

  return (
    <div className="carousel-container">
      <AliceCarousel
        autoPlay
        autoPlayInterval={1000}
        animationDuration={1400}
        infinite
        disableButtonsControls
        disableDotsControls
        responsive={responsive}
        items={items}
      />
    </div>
  );
};

export default Carousel;
