import React from "react";
import { useState } from "react";
import { useGlobalContext } from "../Context/CryptoContext";
import axios from "axios";
import { useEffect } from "react";
import { Coinlist } from "../Config/Api";
import {
  Container,
  Typography,
  TextField,
  ThemeProvider,
  createTheme,
  LinearProgress,
  Table,
  TableHead,
  TableRow,
  TableCell,
  TableBody,
  Stack,
  Pagination,
} from "@mui/material";
import { useNavigate } from "react-router-dom";

const CoinsTable = () => {
  const { currency, currencySymbol } = useGlobalContext();

  const [loading, setLoading] = useState(false);
  const [coinsList, setCoinsList] = useState([]);
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const navigate = useNavigate();

  const fetchCoins = async (url) => {
    setLoading(true);

    // const { data } = await axios.get(url);

    const res = await fetch(url);
    const data = await res.json();
    setCoinsList(data);

    setLoading(false);
  };

  useEffect(() => {
    fetchCoins(Coinlist(currency));
  }, [currency]);

  const darkTheme = createTheme({
    palette: {
      mode: "dark",
    },
  });

  const handleSearch = () => {
    return coinsList.filter(
      (i) =>
        i.name.toLowerCase().includes(search) ||
        i.symbol.toLowerCase().includes(search)
    );
  };

  return (
    <ThemeProvider theme={darkTheme}>
      <div className="coins-container">
        <Container style={{ textAlign: "center" }}>
          <Typography variant="h3">
            Cryptocurrency Prices by Market Cap
          </Typography>

          <TextField
            className="input-search-field"
            label="Search for a crypto currency"
            value={search}
            onChange={(e) => setSearch(e.target.value)}
          />

          {/* Table */}
          {loading ? (
            <LinearProgress color="primary" />
          ) : (
            <>
              <div style={{ overflowX: "scroll" }}>
                <Table>
                  <TableHead style={{ background: "#FFD93D" }}>
                    <TableRow>
                      {["Coin", "Price", "24h Change", "Market Change"].map(
                        (item) => {
                          return (
                            <TableCell
                              key={item}
                              style={{
                                fontWeight: "bold",
                                color: "#070A52",
                                fontSize: "18px",
                              }}
                              align={item === "Coin" ? "" : "left"}
                            >
                              {item}
                            </TableCell>
                          );
                        }
                      )}
                    </TableRow>
                  </TableHead>

                  <TableBody>
                    {handleSearch()
                      .slice((page - 1) * 10, page * 10)
                      .map((coin, index) => {
                        const {
                          image,
                          id,
                          symbol,
                          name,
                          current_price,
                          price_change_percentage_24h,
                          market_cap,
                        } = coin;

                        let profit = price_change_percentage_24h > 0;

                        return (
                          <TableRow
                            key={index}
                            onClick={() => navigate(`/coin/${id}`)}
                            style={{
                              cursor: "pointer",
                            }}
                          >
                            <TableCell
                              style={{
                                display: "flex",
                                gap: 15,
                                alignItems: "center",
                              }}
                            >
                              <img width={"80px"} src={image} alt={name} />
                              <Typography>
                                <span
                                  style={{
                                    textTransform: "uppercase",
                                    fontSize: "26px",
                                    lineHeight: "2.6rem",
                                    fontWeight: "bold",
                                    display: "block",
                                  }}
                                >
                                  {symbol}
                                </span>
                                <span style={{ fontSize: "16px" }}>{name}</span>
                              </Typography>
                            </TableCell>

                            <TableCell>
                              <Typography variant="h6">
                                {currencySymbol}
                                {current_price.toLocaleString("en-IN")}
                              </Typography>
                            </TableCell>

                            <TableCell
                              style={{
                                color: profit ? "#00FFCA" : "#D21312",
                                fontSize: "18px",
                                fontWeight: "bold",
                              }}
                            >
                              {profit
                                ? `+${price_change_percentage_24h.toFixed(2)}%`
                                : price_change_percentage_24h.toFixed(2) + "%"}
                            </TableCell>

                            <TableCell>
                              <p
                                style={{
                                  fontSize: "16px",
                                  padding: "8px",
                                  borderRadius: "4px",
                                  background: "#B9F3E4",
                                  color: "#000",
                                  display: "inline-block",
                                }}
                              >
                                {currencySymbol}
                                {market_cap.toLocaleString("en-IN")}
                              </p>
                            </TableCell>
                          </TableRow>
                        );
                      })}
                  </TableBody>
                </Table>
              </div>

              {/* Pagination */}
              <div
                style={{
                  display: "flex",
                  alignItems: "center",
                  justifyContent: "center",
                  margin: "30px 0",
                }}
              >
                <Stack spacing={2}>
                  <Pagination
                    count={(handleSearch().length / 10).toFixed(0)}
                    color="primary"
                    onChange={(_, pg) => {
                      //(event, page)
                      setPage(pg);
                      document.documentElement.scrollTop = 450;
                    }}
                  />
                </Stack>
              </div>
            </>
          )}
        </Container>
      </div>
    </ThemeProvider>
  );
};

export default CoinsTable;
