import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { useGlobalContext } from "../Context/CryptoContext";
import { SingleCoin } from "../Config/Api";
import { CircularProgress, Typography } from "@mui/material";
import DisplayChart from "../Components/Chart";

const CoinPage = () => {
  const { id } = useParams();
  const [coin, setCoin] = useState();
  const [loading, setLoading] = useState();

  const { currency, currencySymbol } = useGlobalContext();

  const fetchCoin = async (url) => {
    setLoading(true);

    const res = await fetch(url);
    const data = await res.json();

    setCoin(data);
    console.log(coin);
    setLoading(false);
  };

  useEffect(() => {
    fetchCoin(SingleCoin(id));
  }, [currency]);

  return loading ? (
    <CircularProgress color="primary" className="loading" />
  ) : (
    <div className="coinPageContainer">
      {/* Side Bar */}
      <div className="sidebar">
        <div className="header">
          <div className="image">
            <img src={coin?.image.large} alt={coin?.name} />
          </div>
          <Typography variant="h2" className="heading">
            {coin?.name}
          </Typography>
        </div>

        <Typography variant="subtitle1" className="description">
          {coin?.description.en.split(".")[0] + "."}
        </Typography>

        <div className="content">
          <div>
            <Typography variant="h6">Rank: </Typography>
            <Typography variant="p" className="ans">
              {coin?.market_cap_rank}
            </Typography>
          </div>

          <div>
            <Typography variant="h6">Current Price: </Typography>
            <Typography variant="p" className="ans">
              {currencySymbol}
              {coin?.market_data.current_price[
                currency.toLowerCase()
              ].toLocaleString()}
            </Typography>
          </div>

          <div>
            <Typography variant="h6">Market Cap Price: </Typography>
            <Typography variant="p" className="ans">
              {currencySymbol}
              {coin?.market_data.market_cap[
                currency.toLowerCase()
              ].toLocaleString()}
            </Typography>
          </div>
          <div></div>
        </div>
      </div>

      {/* Chart */}
      <div className="chart">
        <DisplayChart coin={coin} />
      </div>
    </div>
  );
};

export default CoinPage;
