import React from "react";
import { useEffect } from "react";
import { useState } from "react";
import { useContext } from "react";
import { createContext } from "react";
import { Currencies } from "../Config/Api";

const CryptCon = createContext();

const CryptoContext = ({ children }) => {
  const [currencies, setCurrencies] = useState([])
  const [currency, setCurrency] = useState("inr");
  const [currencySymbol, setCurrencySymbol] = useState("Rs.");

  const fetchCurrencies = async (url) => {
    const res = await fetch(url);
    const data = await res.json();

    setCurrencies(data)
  }

  useEffect(() => {
    if (currency == "INR") setCurrencySymbol("Rs.");

    fetchCurrencies(Currencies)
  }, []);

  return (
    <CryptCon.Provider value={{ currency, currencySymbol, setCurrency, currencies }}>
      {children}
    </CryptCon.Provider>
  );
};

const useGlobalContext = () => useContext(CryptCon);

export { CryptoContext, useGlobalContext };
