import React, { useEffect, useState } from "react";
import { useGlobalContext } from "../Context/CryptoContext";
import { HistoricalChart } from "../Config/Api";
import { ThemeProvider } from "@emotion/react";
import { Button, createTheme } from "@mui/material";
import { Line, Bar } from "react-chartjs-2";
import {
  Chart,
  CategoryScale,
  PointElement,
  LineElement,
  LinearScale,
  BarElement,
} from "chart.js";
import { buttonData } from "../Config/ChartData";

Chart.register({
  CategoryScale,
  LineElement,
  PointElement,
  LinearScale,
  BarElement,
});

const DisplayChart = ({ coin }) => {
  const [historicalData, setHistoricalData] = useState([]);
  const [days, setDays] = useState(1);

  const { currency } = useGlobalContext();

  const fetchData = async (url) => {
    const res = await fetch(url);
    const data = await res.json();

    setHistoricalData(data.prices);
  };

  useEffect(() => {
    fetchData(HistoricalChart(coin?.id, days, currency));
  }, [days]);

  const darkTheme = createTheme({
    palette: {
      mode: "dark",
    },
  });

  const labels = historicalData.map((elem) => {
    let date = new Date(elem[0]);
    let time =
      date.getHours() > 12
        ? `${date.getHours() - 12} : ${date.getMinutes()}PM`
        : `${date.getHours()} : ${date.getMinutes()} AM`;

    return days === 1 ? time : date.toLocaleDateString();
  });

  const data = historicalData.map((elem) => elem[1]);

  return (
    <ThemeProvider theme={darkTheme}>
      <div className="chartContainer">
        {!historicalData ? (
          "No Data Avalaible"
        ) : (
          <>
            <Line
              data={{
                labels,
                datasets: [
                  {
                    data,
                    label: `Price ( Past ${days} Days ) in ${currency}`,
                    borderColor: "#EEBC1D",
                    // backgroundColor:"#eebc1d"
                  },
                ],
              }}
              options={{
                elments: {
                  point: { radius: 1 },
                },
              }}
            />

            <div className="buttons">
              {buttonData.map((elem) => {
                return (
                  <Button
                    className={`button ${days === elem.days ? "selected" : ""}`}
                    onClick={() => setDays(elem.days)}
                  >
                    {elem.label}
                  </Button>
                );
              })}
            </div>
          </>
        )}
      </div>
    </ThemeProvider>
  );
};

export default DisplayChart;
